.filters {
  width: 15%;
  margin-right: 15px;
  background: #fff;
  padding: 15px;

  .star-button-container {
    text-align: center;
    small {
      color: #aaa;
      margin-bottom: 8px;
      display: inline-block;
    }
  }

  .title {
    margin-top: 2px;
    margin-bottom: 20px;
  }

  &-available-size {
    display: inline-block;
    margin-bottom: 10px;
    /* Customize the label (the container) */
    label {
      display: inline-block;
      position: relative;
      cursor: pointer;
      font-size: 22px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      width: 35px;
      height: 35px;
      font-size: 0.8em;
      margin-bottom: 8px;
      margin-right: 8px;
      border-radius: 50%;
      line-height: 35px;
      text-align: center;

      /* On mouse-over, add a grey background color */
      &:hover input ~ .checkmark {
        border: 1px solid #1b1a20;
      }

      /* When the checkbox is checked, add a blue background */
      & input:checked ~ .checkmark {
        background-color: #1b1a20;
        color: #ececec;
      }

      /* Show the checkmark when checked */
      & input:checked ~ .checkmark:after {
        display: block;
      }

      input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
      }

      /* Create a custom checkbox */
      .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        width: 35px;
        height: 35px;
        font-size: 0.8em;
        border-radius: 50%;
        line-height: 35px;
        text-align: center;
        color: #1b1a20;
        background-color: #ececec;

        border: 1px solid transparent;
      }
    }
  }
}
.filters {
form{
  input[type="text"],
  input[type="password"],
  input[type="number"]{
    width: calc(100% - 32px);
    padding: 0px 15px;
    border: 1px solid #e4e4e4;
    height: 40px;
    border-radius: 3px;
    text-align: center;
    font-size: 14px;
  }
  button{
    height: 40px;
    padding: 0px 50px;
    color: #fff;
    font-size: 14px;
    background: #f62f5e;
    border-radius: 50px;
    border:1px solid #f62f5e;
    margin: 15px auto 0px auto;
    display: block;
    &:hover{
      background: #f8567a;
    }
  }
}
}